<!--
 * @Autor: linxu
 * @Date: 2022-01-18 13:47:11
 * @LastEditors: linxu
 * @LastEditTime: 2022-02-14 17:07:19
 * @Description: BaseTable 组件测试
-->
<template>
  <div>
    <BMainTopComponent />
    <div>
      <span>本地测试引入表格组件库开发</span>
      <br />
      <TableList
        :options="options"
        :columns="columns"
        :dataSource="dataSource"
        :operates="operates(this)"
        :dataTotal="dataTotal"
        :pagination="paginationCfg"
        @handleChangePage="handleChangePage"
        @handleChangeSize="handleChangeSize"
      />
    </div>
  </div>
</template>

<script>
const arr = ['男', '女']
// 表格头设置
const columns = [
  {
    label: '姓名',
    prop: 'name',
    // width: '200',
    minWidth: '35%',
    props: {
      // sortable: 'sortable'
    }
  },
  {
    label: '性别',
    prop: 'sex',
    // width: '200',
    minWidth: '10%',
    formatter: (row, column, index) => {
      return arr[row.sex]
    }
  },
  {
    label: '住址',
    prop: 'address',
    // width: '200',
    minWidth: '35%',
    newjump: (row, columns, index) => {
      return `/syshomePage/home`
    }
  },
  {
    label: '手机号',
    render: (h, parmas) => {
      return [
        h('i', {
          class: 'el-icon-phone-outline',
          style: 'fontSize: 16px; color: red'
        }),
        h('span', parmas.row.mobileNum)
      ]
    },
    // width: '200',
    minWidth: '20%'
  }
]

// 表格操作按钮
const operates = (that) => [
  {
    label: '编辑',
    isShow: (row) => {
      return row.sex !== 2
    },
    disabled: (row) => {
      return row.disabled === 2
    },
    method: (row) => {
      that.handleNewJump(row)
    }
  }
]

// 服务器请求返回值
const creatArray = function (val) {
  let arr = []
  for (let i = 1; i <= val; i++) {
    arr.push(i)
  }
  return arr
}
const dataArr = creatArray(12)
const dataSourceFun = () => {
  return dataArr.map((item, index) => {
    return {
      key: index,
      name: `胡彦斌${item}`,
      age: 32,
      sex: Math.random() < 0.5 ? 0 : 1,
      address: '西湖区湖底公园1号'
    }
  })
}

export default {
  data() {
    return {
      columns,
      operates,
      dataSource: [],
      dataTotal: 0,
      options: {
        index: true,
        labelIndex: '序号',
        props: {
          // 操作列宽度
          width: 120
        }
      },
      paginationCfg: {
        currentPage: 1,
        pageSize: 10,
        layout: 'total, sizes, prev, pager, next, jumper',
        pageSizes: [10, 20, 30, 40]
      },
      data: []
    }
  },
  mounted() {
    this.data = dataSourceFun()
    this.dataTotal = this.data.length
    this.queryTableData()
  },
  methods: {
    queryTableData() {
      const arr = this.data.slice(
        (this.paginationCfg.currentPage - 1) * this.paginationCfg.pageSize,
        this.paginationCfg.currentPage * this.paginationCfg.pageSize
      )
      this.$set(this, 'dataSource', arr)
    },
    handleNewJump(row) {
      console.log(row)
    },
    // 分页 当前页改变
    handleChangePage(val) {
      console.log(`当前页: ${val}`)
      this.paginationCfg.currentPage = val
      this.queryTableData()
    },
    // 分页 每页条数改变
    handleChangeSize(val) {
      console.log(`每页 ${val} 条`)
      this.paginationCfg.currentPage = 1
      this.paginationCfg.pageSize = val
      this.queryTableData()
    }
  }
}
</script>



